.fnts {
  width: 25%;
  height: auto;
  padding: 10px;
  font-size: 20px;
  color: #333;
}

.bgs {
  background-color: #f2f2f2;
  width: 75%;
  height: auto;
  padding: 10px;
  margin-left: 10px;
}

img {
  border-radius: 50%;
}

button {
  height: 25px;
  width: 100px;
  text-align: center;
  line-height: 4px;
  background-color: #4CAF50;
  color: white;
  padding: 14px 20px;
  margin: 8px 22px;
  border: none;
  cursor: pointer;
 
  border-radius: 5px;
  font-size: 18px;
}

input[type=text] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border-radius: 5px;
  font-size: 18px;
}

li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

h2, h4 {
  text-align: center;
  font-size: 30px;
  margin-bottom: 20px;
}
